___CSS_LOADER_EXPORT___.push([module.id, `.task-display-main {
    padding: 4px 16px;
    border-radius: 24px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.task-display-main:hover {
    box-shadow: 1px 1px 1px 1px grey;
}

.side-div {
    display: flex;
    flex-direction: column;
    gap: 12px;
}

.star-container-notes {
    display: flex;
    gap: 4px;
}

.difficulty-div {
    display: flex;
    align-items: center;
    gap: 8px;
}

.finished-task-div, .delete-task-div {
    border-radius: 16px;
    padding: 2px 6px;
    text-align: center;
    cursor: pointer;
}

.finished-task-div:hover, .delete-task-div:hover {
    box-shadow: 1px 1px 1px 1px rgb(167, 167, 167);
    font-size: bold;
}

.finished-task-div {
    color: rgb(1, 105, 1);
    background-color: rgb(184, 230, 202);
    margin: 12px 0px 1px;
}

.delete-task-div {
    color: rgb(181, 3, 3);
    background-color: rgb(221, 167, 167);
}

.notes-notes {
    font-size: 0.8rem;
}`, "",{"version":3,"sources":["webpack://./src/displayNewTask.css"],"names":[],"mappings":"AAAA;IACI,iBAAiB;IACjB,mBAAmB;IACnB,eAAe;IACf,aAAa;IACb,mBAAmB;IACnB,8BAA8B;AAClC;;AAEA;IACI,gCAAgC;AACpC;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;;AAEA;IACI,aAAa;IACb,QAAQ;AACZ;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,QAAQ;AACZ;;AAEA;IACI,mBAAmB;IACnB,gBAAgB;IAChB,kBAAkB;IAClB,eAAe;AACnB;;AAEA;IACI,8CAA8C;IAC9C,eAAe;AACnB;;AAEA;IACI,qBAAqB;IACrB,oCAAoC;IACpC,oBAAoB;AACxB;;AAEA;IACI,qBAAqB;IACrB,oCAAoC;AACxC;;AAEA;IACI,iBAAiB;AACrB","sourcesContent":[".task-display-main {\n    padding: 4px 16px;\n    border-radius: 24px;\n    cursor: pointer;\n    display: flex;\n    align-items: center;\n    justify-content: space-between;\n}\n\n.task-display-main:hover {\n    box-shadow: 1px 1px 1px 1px grey;\n}\n\n.side-div {\n    display: flex;\n    flex-direction: column;\n    gap: 12px;\n}\n\n.star-container-notes {\n    display: flex;\n    gap: 4px;\n}\n\n.difficulty-div {\n    display: flex;\n    align-items: center;\n    gap: 8px;\n}\n\n.finished-task-div, .delete-task-div {\n    border-radius: 16px;\n    padding: 2px 6px;\n    text-align: center;\n    cursor: pointer;\n}\n\n.finished-task-div:hover, .delete-task-div:hover {\n    box-shadow: 1px 1px 1px 1px rgb(167, 167, 167);\n    font-size: bold;\n}\n\n.finished-task-div {\n    color: rgb(1, 105, 1);\n    background-color: rgb(184, 230, 202);\n    margin: 12px 0px 1px;\n}\n\n.delete-task-div {\n    color: rgb(181, 3, 3);\n    background-color: rgb(221, 167, 167);\n}\n\n.notes-notes {\n    font-size: 0.8rem;\n}"],"sourceRoot":""}]);
