.content {
    grid-area: 2 / 2 / -1 / -1;
    display: grid;
    grid-template-columns: 1fr 6fr 2fr;
}

.content-side {
    color: var(--non-important-color, grey);
    font-size: 0.9rem;
    text-align: center;
    padding: 28px;
}

.content-side p:hover {
    cursor: pointer;
    text-decoration: underline;
}

.open-modal {
    display: flex;
    flex-direction: column;
    padding: 28px;
    gap: 24px;
    position: relative;
    top: 25%;
}

.open-modal div {
    display: flex;
    gap: 8px;
    align-items: center;
}

.open-modal-one {
    left: 40%;
}

.open-modal-two {
    left: 39%;
}

.open-modal-three {
    left: 37%;
}

.open-modal-four {
    left: 36%;
}

.open-modal-five {
    left: 35%;
}

.tasks-projects {
    display: flex;
    gap: 16px;
}

.project-icon {
    display: none;
}

.project-icon:not(:checked) + .projects-icon-label {
    filter: brightness(0.8); /* Reset the filter for unchecked labels */
}

.project-icon:checked + .projects-icon-label {
    box-shadow: 2px 2px 1px 1px rgb(86, 85, 85);
}

.projects-icon-label {
    padding: 8px 14px;
    border-radius: 16px;
    cursor: pointer;
    transition: background 0.1s; 
}

.tasks-modal-inputs {
    padding: 2px 6px;
    border-radius: 8px;
    border: 2px solid rgb(168, 166, 166);
    outline: none;
}

.task-modal-stars {
    height: 24px;
    width: 24px;
    fill: gold;
}

#due-date-input {
    border: none;
}

.confirm-container-modal {
    display: flex;
    justify-content: space-between;
    margin-top: 12px;
}

.confirm-container-modal p, .confirm-container-modal input{
    font-size: 0.8rem;
}

.confirm-task-modal {
    color: green;
}

.discard-task-modal {
    color: red;
}

#notes-input {
    padding: 2px 6px;
}

.notes-input-one {
    max-width: 250px !important; 
}

.notes-input-two {
    max-width: 282px !important;
}

.notes-input-three {
    max-width: 314px !important;
}

.notes-input-four {
    max-width: 346px !important;
}

.notes-input-five {
    max-width: 378px !important;
}


.content-main {
    padding: 32px;
    display: flex;
    flex-direction: column;
    gap: 16px;
}

.content-notes {
    padding: 28px 32px;
    display: flex;
    flex-direction: column;
    gap: 16px;
}`, "",{"version":3,"sources":["webpack://./src/content.css"],"names":[],"mappings":";;AAEA;IACI,0BAA0B;IAC1B,aAAa;IACb,kCAAkC;AACtC;;AAEA;IACI,uCAAuC;IACvC,iBAAiB;IACjB,kBAAkB;IAClB,aAAa;AACjB;;AAEA;IACI,eAAe;IACf,0BAA0B;AAC9B;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,aAAa;IACb,SAAS;IACT,kBAAkB;IAClB,QAAQ;AACZ;;AAEA;IACI,aAAa;IACb,QAAQ;IACR,mBAAmB;AACvB;;AAEA;IACI,SAAS;AACb;;AAEA;IACI,SAAS;AACb;;AAEA;IACI,SAAS;AACb;;AAEA;IACI,SAAS;AACb;;AAEA;IACI,SAAS;AACb;;AAEA;IACI,aAAa;IACb,SAAS;AACb;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,uBAAuB,EAAE,0CAA0C;AACvE;;AAEA;IACI,2CAA2C;AAC/C;;AAEA;IACI,iBAAiB;IACjB,mBAAmB;IACnB,eAAe;IACf,2BAA2B;AAC/B;;AAEA;IACI,gBAAgB;IAChB,kBAAkB;IAClB,oCAAoC;IACpC,aAAa;AACjB;;AAEA;IACI,YAAY;IACZ,WAAW;IACX,UAAU;AACd;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,8BAA8B;IAC9B,gBAAgB;AACpB;;AAEA;IACI,iBAAiB;AACrB;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,UAAU;AACd;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI,2BAA2B;AAC/B;;AAEA;IACI,2BAA2B;AAC/B;;AAEA;IACI,2BAA2B;AAC/B;;AAEA;IACI,2BAA2B;AAC/B;;AAEA;IACI,2BAA2B;AAC/B;;;AAGA;IACI,aAAa;IACb,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;;AAEA;IACI,kBAAkB;IAClB,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb","sourcesContent":["\n\n.content {\n    grid-area: 2 / 2 / -1 / -1;\n    display: grid;\n    grid-template-columns: 1fr 6fr 2fr;\n}\n\n.content-side {\n    color: var(--non-important-color, grey);\n    font-size: 0.9rem;\n    text-align: center;\n    padding: 28px;\n}\n\n.content-side p:hover {\n    cursor: pointer;\n    text-decoration: underline;\n}\n\n.open-modal {\n    display: flex;\n    flex-direction: column;\n    padding: 28px;\n    gap: 24px;\n    position: relative;\n    top: 25%;\n}\n\n.open-modal div {\n    display: flex;\n    gap: 8px;\n    align-items: center;\n}\n\n.open-modal-one {\n    left: 40%;\n}\n\n.open-modal-two {\n    left: 39%;\n}\n\n.open-modal-three {\n    left: 37%;\n}\n\n.open-modal-four {\n    left: 36%;\n}\n\n.open-modal-five {\n    left: 35%;\n}\n\n.tasks-projects {\n    display: flex;\n    gap: 16px;\n}\n\n.project-icon {\n    display