___CSS_LOADER_EXPORT___.push([module.id, `.header {
    display: grid;
    grid-template-columns: 1fr 6fr 2fr;
    background-color: var(--main-color);
    color: var(--main-text-color);
}

.header-container {
    grid-area: 1 / 2 / 2/ 3;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 16px;
    padding: 28px 0px;
}

.progress-container {
    display: flex;
    align-items: center;
    gap: 16px;
}

.progress {
    width: 600px;
    height: 40px;
    border: 2px solid white;
    border-radius: 24px;
}

.user-settings {
    grid-area: 1 / 3 / 2 / -1;
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
    padding: 40px 48px;
}

.user-settings svg {
    fill: var(--main-text-color, white);
    height: 30px;
}

.user-settings svg:hover {
    cursor: pointer;
}

.modal-open {
    padding: 28px;
    border: 2px solid black;
    top: 35%;
    left: 45%;
    display: flex;
    flex-direction: column;
    gap: 12px;
}

.modal-close {
    display: none;
}

.inputs div {
    display: flex;
    gap: 8px;
}

.finish {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.save, .discard {
    font-size: 0.85rem;
}

.save:hover, .discard:hover {
    cursor: pointer;
}

.save {
    color: green;
}

.discard {
    color: red;
}`, "",{"version":3,"sources":["webpack://./src/header.css"],"names":[],"mappings":"AAAA;IACI,aAAa;IACb,kCAAkC;IAClC,mCAAmC;IACnC,6BAA6B;AACjC;;AAEA;IACI,uBAAuB;IACvB,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,uBAAuB;IACvB,SAAS;IACT,iBAAiB;AACrB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,SAAS;AACb;;AAEA;IACI,YAAY;IACZ,YAAY;IACZ,uBAAuB;IACvB,mBAAmB;AACvB;;AAEA;IACI,yBAAyB;IACzB,aAAa;IACb,qBAAqB;IACrB,yBAAyB;IACzB,kBAAkB;AACtB;;AAEA;IACI,mCAAmC;IACnC,YAAY;AAChB;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,QAAQ;IACR,SAAS;IACT,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,aAAa;IACb,QAAQ;AACZ;;AAEA;IACI,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;AACvB;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,UAAU;AACd","sourcesContent":[".header {\n    display: grid;\n    grid-template-columns: 1fr 6fr 2fr;\n    background-color: var(--main-color);\n    color: var(--main-text-color);\n}\n\n.header-container {\n    grid-area: 1 / 2 / 2/ 3;\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    justify-content: center;\n    gap: 16px;\n    padding: 28px 0px;\n}\n\n.progress-container {\n    display: flex;\n    align-items: center;\n    gap: 16px;\n}\n\n.progress {\n    width: 600px;\n    height: 40px;\n    border: 2px solid white;\n    border-radius: 24px;\n}\n\n.user-settings {\n    grid-area: 1 / 3 / 2 / -1;\n    display: flex;\n    align-items: flex-end;\n    justify-content: flex-end;\n    padding: 40px 48px;\n}\n\n.user-settings svg {\n    fill: var(--main-text-color, white);\n    height: 30px;\n}\n\n.user-settings svg:hover {\n    cursor: pointer;\n}\n\n.modal-open {\n    padding: 28px;\n    border: 2px solid black;\n    top: 35%;\n    left: 45%;\n    display: flex;\n    flex-direction: column;\n    gap: 12px;\n}\n\n.modal-close {\n    display: none;\n}\n\n.inputs div {\n    display: flex;\n    gap: 8px;\n}\n\n.finish {\n    display: flex;\n    justify-content: space-between;\n    align-items: center;\n}\n\n.save, .disc