___CSS_LOADER_EXPORT___.push([module.id, `.sidebar {
    grid-area: 1 / 1 / -1 / 2;
    background-color: var(--sidebar-bck-color);
    padding: 36px 28px;
    display: flex;
    flex-direction: column;
    gap: 36px;
}

.projects {
    display: flex;
    flex-direction: column;
    gap: 8px;
}

.default-options {
    display: flex;
    flex-direction: column;
    gap: 6px;
}

.default-options svg {
    height: 1.5rem;
}

.default-options div {
    display: flex;
    align-items: center;
    gap: 12px;
}

.default-options div:hover {
    background-color: var(--sidebar-hover-color);
    cursor: pointer;
    font-weight: bold;
}

.new-project-div {
    display: flex;
    align-items: center;
    gap: 8px;
    margin-right: -24px;
}

.new-project-div input {
    height: 18px;
    padding: 2px 8px;
    border-radius: 12px;
    border: none;
}

.new-project-div svg{
    height: 14px;
}

.new-project-div svg:hover, .close-button:hover {
    height: 18px;
    font-size: 1.25rem;
    cursor: pointer;
}

.projects-title {
    display: flex;
    align-items: center;
    gap: 8px;
    margin-bottom: 4px;
}

#add-project {
    height: 18px;
    width: 18px;
    border: none;
    border-radius: 12px;
    font-size: 0.9rem;
}

#add-project:hover {
    font-size: 1.05rem;
    cursor: pointer;
    background-color: var(--sidebar-hover-color);
}

.new-project-div .color-picker {
    padding: 0px;
    height: 22px;
    width: 20px;
}

.project-container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 20px;
    cursor: pointer;
    margin-bottom: 1px;
}

.project:hover {
    box-shadow: 1px 1px 1px 1px rgb(177, 176, 176);
}

.project {
    border-radius: 20px;
    padding: 4px 12px;
    width: 160px;
}

.close-button {
    display: flex;
    align-items: center;
    width: 14px;
    height: 14px;
    color: red;
    font-size: 1rem;
    font-weight: bold;
    background-color: var(--sidebar-bck-color);

}

.remove-button {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 6px;
    color: red;
    font-size: 1rem;
    border: none;
    display: none;
    font-weight: bold;
}

.remove-button:hover {
    cursor: pointer;
    font-size: 1.15rem;
}`, "",{"version":3,"sources":["webpack://./src/sidebar.css"],"names":[],"mappings":"AAAA;IACI,yBAAyB;IACzB,0CAA0C;IAC1C,kBAAkB;IAClB,aAAa;IACb,sBAAsB;IACtB,SAAS;AACb;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,QAAQ;AACZ;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,QAAQ;AACZ;;AAEA;IACI,cAAc;AAClB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,SAAS;AACb;;AAEA;IACI,4CAA4C;IAC5C,eAAe;IACf,iBAAiB;AACrB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,QAAQ;IACR,mBAAmB;AACvB;;AAEA;IACI,YAAY;IACZ,gBAAgB;IAChB,mBAAmB;IACnB,YAAY;AAChB;;AAEA;IACI,YAAY;AAChB;;AAEA;IACI,YAAY;IACZ,kBAAkB;IAClB,eAAe;AACnB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,QAAQ;IACR,kBAAkB;AACtB;;AAEA;IACI,YAAY;IACZ,WAAW;IACX,YAAY;IACZ,mBAAmB;IACnB,iBAAiB;AACrB;;AAEA;IACI,kBAAkB;IAClB,eAAe;IACf,4CAA4C;AAChD;;AAEA;IACI,YAAY;IACZ,YAAY;IACZ,WAAW;AACf;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,8BAA8B;IAC9B,mBAAmB;IACnB,eAAe;IACf,kBAAkB;AACtB;;AAEA;IACI,8CAA8C;AAClD;;AAEA;IACI,mBAAmB;IACnB,iBAAiB;IACjB,YAAY;AAChB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,WAAW;IACX,YAAY;IACZ,UAAU;IACV,eAAe;IACf,iBAAiB;IACjB,0CAA0C;;AAE9C;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,iBAAiB;IACjB,UAAU;IACV,eAAe;IACf,YAAY;IACZ,aAAa;IACb,iBAAiB;AACrB;;AAEA;IACI,eAAe;IACf,kBAAkB;AACtB","sourcesContent":[".sidebar {\n    grid-area: 1 / 1 / -1 / 2;\n    background-color: var(--sidebar-bck-color);\n    padding: 36px 28px;\n    display: flex;\n    flex-direction: column;\n    gap: 36px;\n}\n\n.projects {\n    display: flex;\n    flex-direction: column;\n    gap: 8px;\n}\n\n.default-options {\n    display: flex;\n    flex-direction: column;\n    gap: 6px;\n}\n\n.default-options svg {\n    height: 1.5rem;\n}\n\n.default-options div {\n    display: flex;\n    align-items: center;\n    gap: 12px;\n}\n\n.default-options div:hover {\n    background-color: var(--sidebar-hover-color);\n    cursor: pointer;\n    font-weight: bold;\n}\n\n.new-project-div {\n    display: flex;\n    align-items: center;\n    gap: 8px;\n    margin-right: -24px;\n}\n\n.new-project-div input {\n    height: 18px;\n    padding: 2px 8px;\n   